<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="120"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">Documentos en línea</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Categoría > Subcategoría > Tipo de pago > Tarjeta > Datos</v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">{{ operation.name }}</div>

          <div class="call-to-action">
            <div class="text text-center" style="display: block">
              <div v-if="!operation.document.variable_price">Costo del documento : S/. {{ operation.price }}</div>
              <div>Código de solicitud: {{ operation.id | padleft }}</div>
              <div>Abogado responsable: {{ operation.lawyer.name }}</div>
            </div>
          </div>

          <div class="page-title">Datos del documento solicitado</div>
          <v-row>
            <v-col cols="12">
              <v-combobox v-model="operation.ubigeo_department_id" label="Departamento" :items="departments"
                          item-text="name"
                          hide-details item-value="id"></v-combobox>
            </v-col>

            <!-- form data start -->
            <v-col v-for="field in operationFormData" :key="field.id" cols="12">

              <!-- Text -->
              <v-text-field v-if="field.type === 'Text'" v-model="field.value" :label="field.label"
                            hide-details></v-text-field>

              <!-- Textarea -->
              <v-textarea v-if="field.type === 'Textarea'" v-model="field.value" :label="field.label"
                          hide-details></v-textarea>

              <!-- Select -->
              <v-combobox v-if="field.type === 'Select'" v-model="field.value" :label="field.label"
                          :items="field.options"
                          hide-details item-value="id" item-text="text"></v-combobox>

              <!-- SelectMultiple -->
              <v-combobox v-if="field.type === 'SelectMultiple'" v-model="field.value" :label="field.label"
                          :items="field.options" multiple
                          hide-details item-value="id" item-text="text"></v-combobox>

              <!-- File -->
              <v-file-input v-if="field.type === 'File'" v-model="field.value" :label="field.label"
                            hide-details></v-file-input>
            </v-col>
            <!-- form data end -->

            <v-col cols="12">
              <v-btn @click="goToDataConfirm" :loading="isLoading" color="primary" block>
                Continuar
              </v-btn>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";
import axios from 'axios'

export default {
  name: 'DocumentsData',
  data: () => ({
    isLoading: false,
    specialties: [],
    departments: [],
    operation: {document: '', lawyer: ''},
    operationFormData: '',
  }),
  computed: {
    ...mapGetters(['user']),
    operationId() {
      return this.$route.params.id
    }
  },
  created() {
    this.loadSpecialties()
    this.loadDepartments()
    this.loadOperation()
  },
  methods: {
    ...mapActions([]),
    loadOperation() {
      axios.get('operations/' + this.operationId).then((response) => {
        this.operation = response.data

        if (this.operation.lawyer_specialty_id > 0) {
          this.operation.lawyer_specialty_id = this.specialties.find((obj) => obj.id === this.operation.lawyer_specialty_id)
        }

        if (this.operation.ubigeo_department_id > 0) {
          this.operation.ubigeo_department_id = this.departments.find((obj) => obj.id === parseInt(this.operation.ubigeo_department_id))
        }

        this.loadFormData()
        //this.selectOperation(response.data)
      })
    },
    loadSpecialties() {
      let url = 'specialties'
      axios.get(url).then((response) => {
        this.specialties = response.data
      }).catch().then(() => {

      })
    },
    loadDepartments() {
      let url = 'ubigeo/departments'
      axios.get(url).then((response) => {
        this.departments = response.data
      }).catch().then(() => {

      })
    },
    loadFormData() {
      let url = 'external-resources-forms/' + this.operation.resource_id + '?operation_id=' + this.operation.id
      axios.get(url).then((response) => {
        //this.selectOperationFormData(response.data)
        this.operationFormData = response.data
      }).catch().then(() => {

      })
    },
    goToDataConfirm() {
      // save data
      //this.selectOperation(this.operation)
      //this.selectOperationFormData(this.operationFormData)

      // save data
      this.isLoading = true
      let data = {
        //lawyer_specialty_id: this.operation.lawyer_specialty_id.id,
        ubigeo_department_id: this.operation.ubigeo_department_id.id,
        fields: this.operationFormData
      }

      let formData = new FormData();
      formData.append('_method', 'PUT');
      formData.append('ubigeo_department_id', this.operation.ubigeo_department_id.id);
      formData.append('fields', this.operationFormData);

      let config = {headers: {'Content-Type': 'multipart/form-data'}};

      axios.post('external-resources-forms/' + this.operation.id, data, config).then(() => {
        // go to confirm screen
        this.$router.push({name: 'DocumentsDataConfirm', params: {id: this.operation.id}})
      }).catch(() => {
      }).then(() => {
        this.isLoading = false
      })
    },
  }
}
</script>
